@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Roboto", sans-serif;
}

body {
  margin: 0;
  /* background-color: #dbd4d4; */

  width: 100%;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.screen_screen__1sfdW {
  padding: 10px 10px 0 10px;
  width: 300px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.screen_previousOperand__362Ew,
.screen_currentOperand__2GYa3 {
  padding: 5px;
  width: 100%;
  text-align: right;
  border: none;
  color: rgb(157, 157, 157);
  border-radius: 10px;
  font-family: "Roboto", sans-serif !important;
  outline: none;
}

.screen_previousOperand__362Ew {
  font-size: 20px !important;
  margin-bottom: 10px;
  box-shadow: rgba(99, 98, 98, 0.12) 0px 1px 3px,
    rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
.screen_currentOperand__2GYa3 {
  font-size: 32px !important;
  box-shadow: inset 2px 2px 5px #babecc, inset -5px -5px 10px #ffffff73;
}
::-webkit-input-placeholder {
  color: rgb(157, 157, 157);
}
:-ms-input-placeholder {
  color: rgb(157, 157, 157);
}
::placeholder {
  color: rgb(157, 157, 157);
}

.Key_key__1gkzH {
  cursor: pointer;
  border: none;
  outline: none;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 24px;
  font-family: "Roboto", sans-serif !important;
  font-weight: 300;
  border-radius: 10px;
  background: transparent;
  box-shadow: rgba(99, 98, 98, 0.12) 0px 1px 3px,
    rgba(0, 0, 0, 0.24) 0px 1px 2px;
  color: rgb(72, 72, 72);
}
.Key_key__1gkzH:nth-last-child(3) {
  grid-column: 1/3;
}
.Key_key__1gkzH:nth-last-child(1) {
  border-radius: 10px;
}
.Key_key__1gkzH:nth-child(4),
.Key_key__1gkzH:nth-child(8),
.Key_key__1gkzH:nth-child(12),
.Key_key__1gkzH:nth-child(16),
.Key_key__1gkzH:nth-child(19) {
  background-color: #fd965b;
  color: white;
}

.Keyboard_keyboard__LrzZZ {
  display: grid;
  height: 300px;
  width: 300px;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-gap: 10px;
  padding: 10px;
  border-radius: 0 0 10px 10px;
}

.Calculator_calculator__1wcgR {
  display: block;
  width: auto;
  /* box-shadow: rgba(99, 98, 98, 0.12) 0px 1px 3px,
    rgba(0, 0, 0, 0.24) 0px 1px 2px; */
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
  background: white;
  border-radius: 10px;
}

